* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  flex-direction: column;
  align-items: center;
  height: 100vh;
  font-family: Roboto, sans-serif;
  display: flex;
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding-top: 32px;
  transition: transform .3s;
  display: flex;
}

.card__img {
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.card__title {
  color: #060b35;
  align-items: flex-start;
  width: 100%;
  margin-bottom: 4px;
  padding: 0 16px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product {
  color: #616070;
  align-items: center;
  width: 100%;
  margin-bottom: 16px;
  padding: 0 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.stars {
  gap: 4px;
  display: flex;
}

.stars__star {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars--4 .stars__star:nth-child(n+5) {
  filter: grayscale();
}

.main {
  --column-count: 1;
  grid-template-columns: repeat(var(--column-count), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .main {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .main {
    --column-count: 4;
  }
}

@media (width >= 1200px) {
  .main {
    --column-count: 4;
    padding: 50px 128px;
  }
}

.header {
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  margin: 0;
  padding: 0;
  display: block;
}

.header__logo-img {
  width: 40px;
  height: 40px;
  margin: 0;
  padding: 0;
  display: block;
}

.button {
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  background: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin-bottom: 16px;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
  display: flex;
}

.button:hover {
  color: #00acdc;
  background: #fff;
  border: 1px solid #00acdc;
}

.reviews {
  color: #060b35;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 24px;
  padding: 0 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.price {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 18px;
  margin-bottom: 16px;
  padding: 0 16px;
  display: flex;
}

.price__title {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
}

.price__money {
  font-size: 16px;
  font-weight: 700;
}

.nav {
  margin-left: auto;
}

.nav_list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav_item {
  margin-left: 20px;
}

.nav_item:first-child {
  margin-left: 0;
}

.nav_link {
  color: #060b35;
  white-space: nowrap;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav_link.is-active, .nav_link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  white-space: nowrap;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.is-active:hover {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
